<template>
<div>
  <!-- <button @click="login()"></button>
  <button @click="logout()"></button>
  <button @click="getImg()"></button> -->
  <img id="img" />
</div>
</template>

<script>
var warningImg =
  'data:image/png;base64,'
let instance = axios.create()
const component = {
  name: 'work-flow',
  data() {
    return {}
  },
  props: {
    requestUrl: String,
    instId: String,
    defId: String,
    account: String
  },
  install: (Vue, options) =>
    Vue.component(
      (options ? options.prefix : '') + component.name,
      component,
    ),
  methods: {
    imageEncode(arrayBuffer, mimetype) {
      console.debug(typeof arrayBuffer)
      let u8 = new Uint8Array(arrayBuffer)
      let b64encoded = btoa(
        [].reduce.call(
          new Uint8Array(arrayBuffer),
          function(p, c) {
            return p + String.fromCharCode(c)
          },
          ''
        )
      )
      return 'data:' + mimetype + ';base64,' + b64encoded
    },
    login() {
      return new Promise((resolve, reject) => {
        var params = {
          password: this.account,
          account: this.account
        }
        instance.get('org/login/valid', {
          params,
          withCredentials: true
        }).then(resp => {
          console.debug(resp)
          resolve()
        })
      })
    },
    logout() {
      instance.get('logout', {
        withCredentials: true
      })
    },
    getImg() {
      return new Promise((resolve, reject) => {
        var params = {
          instId: this.instId,
          defId: this.defId
        }
        instance.get('bpm/instance/flowImage', {
            params,
            withCredentials: true,
            responseType: 'arraybuffer'
          })
          .then(resp => {
            img.src = resp.headers['content-type'].indexOf('image') > -1 ? this.imageEncode(resp.data, resp.headers['content-type']) : warningImg
            resolve()
          })
      })
    },
    refresh() {
      this.login().then(() => {
        this.getImg().then(() => this.logout())
      })
    },

    // async test() {
    //   await this.login()
    //   await this.getImg()
    //   await this.logout()
    // }
  },
  created() {
    instance.defaults.baseURL = this.requestUrl
  },
  mounted() {
    this.refresh()
    // this.test()
  }
}
export default component
</script>

